.page__body {
  background: #485563; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #29323c,
    #485563
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  font-family: 'Alata', sans-serif;
}

.modal {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 999;
}
.modal-body {
  position: relative;
  padding: 20px;
  border-radius: 10px;
  background-color: white;
}
.modal__img {
  width: 300px;
}
.modal__name {
  position: absolute;
  top: -44px;
  left: 50%;
  transform: translate(-50%, 0);
  background-color: white;
  padding: 10px;
  border-radius: 10px;
}
.stat-list {
  list-style: none;
  padding: 0;
}
.stat-list__item {
  display: flex;
  justify-content: space-between;
  padding-bottom: 5px;
}
.stat-list__item:not(:last-child) {
  border-bottom: .5px solid lightgray;
}

@media screen and (max-width: 320px) {
  .modal__img {
    width: 250px;
  }
}
.card {
  border-radius: 7px;
  padding: 10px;
  cursor: pointer;
  border: none;
  min-height: 350px;
}
.card__figure {
  text-align: center;
  position: relative;
}
.card__image {
  max-width: 150px;
  z-index: 1;
  position: relative;
}
.card__image-circle {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  z-index: 0;
  width: 160px;
  height: 160px;
  background-color: rgba(255,255,255,.8);
  border-radius: 50%;
}
.card__name {
  text-align: center;
}
.card__types {
  text-align: center;
}

