.lds-roller {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.lds-roller div {
  -webkit-animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
          animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  transform-origin: 40px 40px;
}
.lds-roller div:after {
  content: " ";
  display: block;
  position: absolute;
  width: 7px;
  height: 7px;
  border-radius: 50%;
  background: #fff;
  margin: -4px 0 0 -4px;
}
.lds-roller div:nth-child(1) {
  -webkit-animation-delay: -0.036s;
          animation-delay: -0.036s;
}
.lds-roller div:nth-child(1):after {
  top: 63px;
  left: 63px;
}
.lds-roller div:nth-child(2) {
  -webkit-animation-delay: -0.072s;
          animation-delay: -0.072s;
}
.lds-roller div:nth-child(2):after {
  top: 68px;
  left: 56px;
}
.lds-roller div:nth-child(3) {
  -webkit-animation-delay: -0.108s;
          animation-delay: -0.108s;
}
.lds-roller div:nth-child(3):after {
  top: 71px;
  left: 48px;
}
.lds-roller div:nth-child(4) {
  -webkit-animation-delay: -0.144s;
          animation-delay: -0.144s;
}
.lds-roller div:nth-child(4):after {
  top: 72px;
  left: 40px;
}
.lds-roller div:nth-child(5) {
  -webkit-animation-delay: -0.18s;
          animation-delay: -0.18s;
}
.lds-roller div:nth-child(5):after {
  top: 71px;
  left: 32px;
}
.lds-roller div:nth-child(6) {
  -webkit-animation-delay: -0.216s;
          animation-delay: -0.216s;
}
.lds-roller div:nth-child(6):after {
  top: 68px;
  left: 24px;
}
.lds-roller div:nth-child(7) {
  -webkit-animation-delay: -0.252s;
          animation-delay: -0.252s;
}
.lds-roller div:nth-child(7):after {
  top: 63px;
  left: 17px;
}
.lds-roller div:nth-child(8) {
  -webkit-animation-delay: -0.288s;
          animation-delay: -0.288s;
}
.lds-roller div:nth-child(8):after {
  top: 56px;
  left: 12px;
}
@-webkit-keyframes lds-roller {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes lds-roller {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.pokemon__list {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-gap: 10px;
  gap: 10px;
  margin-top: 10px;
}

@media screen and (max-width: 1325px) {
  .pokemon__list {
    grid-template-columns: repeat(4,1fr);
  }
}

@media screen and (max-width: 1065px) {
  .pokemon__list {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media screen and (max-width: 805px) {
  .pokemon__list {
    grid-template-columns: 1fr 1fr;
  }
}

@media screen and (max-width: 550px) {
  .pokemon__list {
    grid-template-columns: minmax(250px, 1fr);
  }
}
.buttons__page {
  display: flex;
  margin-top: 10px;
}
.button__page {
  padding: 10px 20px;
  font-family: inherit;
  font-size: 14px;
  font-weight: bold;
  cursor: pointer;
  border: none;
  color: white;
}
.button__page-prev {
  background-color: #FF4B2B;
}
.button__page-prev:hover {
  background-color: #ff5e41
}
.button__page-next {
  background-color: #2948ff;
  margin-left: auto;
}
.button__page-next:hover {
  background-color: #3957ff;
}
.search {
  display: flex;
}
.search__input {
  padding: 10px;
  flex-basis: 90%;
  font-size: 14px;
  font-family: inherit;
}
.search__button {
  font-weight: bold;
  font-family: inherit;
  color: white;
  flex-basis: 10%;
  margin-left: 10px;
  background-color: #02a1d6;
  border: none;
  outline: none;
  cursor: pointer;
  transition: all .1s ease;
}
.search__button:hover,
.search__button:focus {
  background-color: #0285b1;
}
.search__button:active {
  background-color: #0cbffa;
}
.categories {
  display: flex;
}
.categories-list {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin: 0 10px;
  padding: 0;
}
.categories__text {
  color: white;
}
.categories-list__button {
  color: white;
  text-transform: capitalize;
  outline: none;
  cursor: pointer;
  background: none;
  border: none;
}
.categories-list__button:focus {
  color: rgb(187, 187, 187);
}
.categories-lsit__button-text:not(.categories-lsit__button-text--active) {
  border-bottom: 1px dashed skyblue;
}

@media screen and (max-width: 650px) {
.categories {
  flex-direction: column;
}
}
